<template>
  <div>
    <form>
      <input v-model="todo.name" placeholder="Task's name" /><br><br>
      <button @click="validate">Add Task To Do</button>
    </form>
    <button @click="goBack">Back to List</button>
    <br>
    <br>
    <div v-show="!valid">Task must have a name!</div>
  </div>
  
</template>
<script>
export default {
  data() {
    return {
      todo: {
        name: null,
        checked: false,
      },
      valid: true,
    };
  },
  methods: {
    validate() {
      this.valid = true;
      if (this.todo.name != null) {
        this.$router.push({name:'mainPage', params:{newTodoName: this.todo.name}})
      } else {
        this.valid = false;
        setTimeout(() => {
          this.valid = true;
        }, 5000);
      }
    },
          goBack(){
          this.$router.push({name:'mainPage'})
      }
  },
};
</script>
